export default {
	name: 'Word',
	computed:{
		word() {
			let	_this = this
			if(this.$store.getters.isLock){ //如果lock为true，返回变化之前的值
				return _this.hold
			}else{
				//当lock为false，保存变化之前的值
				_this.hold = this.$store.getters.word
				return this.$store.getters.word
			}
			
		}
		
	},
	data() {
		return {
			isChange: false,
			innerAudioContext: null,
			hold: null
		}
	},
	methods: {
		tapSound() {
			this.isChange = !this.isChange
		},
		playAudio() {//播放音频
			if (!this.isChange) {
				this.innerAudioContext.src = this.word.soundmark[0].audio;
			} else {
				this.innerAudioContext.src = this.word.soundmark[1].audio;
			}
			this.innerAudioContext.play(() => {})
		}
	},
	created() {
		console.log(this.$store.getters.word)
		
	},
	mounted() {//加载音频，若需要自动播放，则自动播放
		this.innerAudioContext = uni.createInnerAudioContext();
		this.innerAudioContext.autoplay = this.word.auto_audio;
		if (this.word.is_kk) {
			this.innerAudioContext.src = this.word.soundmark[0].audio;
		} else {
			this.innerAudioContext.src = this.word.soundmark[1].audio;
		}
	}
}
